.create-text {
  height: 400px;
  display: flex;
  flex-direction: column;

  .canvas {
    position: absolute;
    left: 0;
    top: 0;
    // bottom: 20px;
    // right: 20px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border: 1px solid black;
  }
  .canvas.draw {
    touch-action: none;
  }
  .wrap {
    // margin: 20px;
    padding: 0;
  }
  .container {
    position: relative;
    // margin: 20px;
    // height: 400px;
    flex: 1;
  }
  .markdown-body {
    margin: 0;
    padding: 0;
  }
  .btn {
    position: absolute;
    right: 20px;
    top: 20px;
    background-color: rgba(255, 255, 255, 0.7);
    user-select: none;
    border: none;
    padding: 6px 12px;
    box-shadow: 0px 7px 14px rgba(0, 0, 0, 0.05),
      0px 0px 3.12708px rgba(0, 0, 0, 0.0798),
      0px 0px 0.931014px rgba(0, 0, 0, 0.1702);
  }
  .clear {
    right: 120px;
  }
  .textarea {
    position: absolute;
    display: inline-block;
    backface-visibility: hidden;
    opacity: 0;
    margin: 0px;
    padding: 0px;
    border: 0px;
    outline: 0px;
    resize: none;
    font-weight: 500 !important;
    // text-align: center;
    background: grey;
    overflow: hidden;
  }
  .textarea_copy {
    position: fixed;
    left: 0 !important;
    top: 0 !important;
  }
}

